<template>
	<view>
		<view class="top">
			<image :src="bgImg" mode="" class="bg-img"></image>
			<u-icon name="plus-circle" color="#fff" size="60" class="upload-icon"></u-icon>
			<image :src="avatar" mode="" class="avatar-img"></image>
			<text class="nickname">{{ name }}</text>
		</view>
		<view class="content">
			<view class="content-top">
				<u-tabs-swiper ref="uTabs" :list="list" :current="current" @change="tabsChange" :is-scroll="false" bar-width="70" active-color="rgb(133, 203, 248)"></u-tabs-swiper>
			</view>
			<swiper class="message" :current="swiperCurrent" @transition="transition" @animationfinish="animationfinish">
				<swiper-item v-for="(item, index) in messageData" :key="index">
					<scroll-view scroll-y class="message-content" @scrolltolower="onreachBottom"><message-item :messageData="item.message" @change-follow="changeFollow(index,$event)"></message-item></scroll-view>
				</swiper-item>
			</swiper>
		</view>
	</view>
</template>

<script>
export default {
	data() {
		return {
			bgImg: 'http://browser9.qhimg.com/bdm/800_450_85/t010769fafba59eb5c4.jpg',
			avatar: 'http://browser9.qhimg.com/bdm/800_450_85/t010769fafba59eb5c4.jpg',
			name: '张三丰',
			// tab栏数据
			list: [
				{
					name: '热门'
				},
				{
					name: '关注'
				}
			],
			// 因为内部的滑动机制限制，请将tabs组件和swiper组件的current用不同变量赋值
			current: 0, // tabs组件的current值，表示当前活动的tab选项
			swiperCurrent: 0, // swiper组件的current值，表示当前那个swiper-item是活动的
			// 全部留言数据
			messageData: [
				{
					message: [
						{
							id: 1,
							avatar: 'http://browser9.qhimg.com/bdm/800_450_85/t010769fafba59eb5c4.jpg',
							name: '哈哈',
							time: 1614689655857,
							isFollow: false,
							message: '阿达所得税法法撒飒飒发傻舒服舒服飒飒哈哈哈哈哈啊哈哈',
							img: ['http://browser9.qhimg.com/bdm/800_450_85/t010769fafba59eb5c4.jpg','http://browser9.qhimg.com/bdm/800_450_85/t010769fafba59eb5c4.jpg', 'http://browser9.qhimg.com/bdm/800_450_85/t010769fafba59eb5c4.jpg'],
							comment: 5,
							like: 10,
							isLike:true
						},
						{
							id: 2,
							avatar: 'http://browser9.qhimg.com/bdm/800_450_85/t010769fafba59eb5c4.jpg',
							name: '哈哈',
							time: 1614689655857,
							isFollow: false,
							message: '阿达所得税法法撒飒飒发傻舒服舒服飒飒哈哈哈哈哈啊哈哈',
							img: ['http://browser9.qhimg.com/bdm/800_450_85/t010769fafba59eb5c4.jpg', 'http://browser9.qhimg.com/bdm/800_450_85/t010769fafba59eb5c4.jpg'],
							comment: 5,
							like: 10,
							isLike:false
						}
					]
				},
				{
					message: [
						{
							id: 1,
							avatar: 'http://browser9.qhimg.com/bdm/800_450_85/t010769fafba59eb5c4.jpg',
							name: '哈哈',
							time: 1614689655857,
							isFollow: false,
							message: '阿达所得税法法撒飒飒发傻舒服舒服飒飒哈哈哈哈哈啊哈哈',
							img: ['http://browser9.qhimg.com/bdm/800_450_85/t010769fafba59eb5c4.jpg', 'http://browser9.qhimg.com/bdm/800_450_85/t010769fafba59eb5c4.jpg'],
							comment: 5,
							like: 10,
							isLike:false
						},
						{
							id: 2,
							avatar: 'http://browser9.qhimg.com/bdm/800_450_85/t010769fafba59eb5c4.jpg',
							name: '哈哈',
							time: 1614689655857,
							isFollow: false,
							message: '阿达所得税法法撒飒飒发傻舒服舒服飒飒哈哈哈哈哈啊哈哈',
							img: ['http://browser9.qhimg.com/bdm/800_450_85/t010769fafba59eb5c4.jpg', 'http://browser9.qhimg.com/bdm/800_450_85/t010769fafba59eb5c4.jpg'],
							comment: 5,
							like: 10,
							isLike:false
						}
					]
				}
			]
		};
	},
	methods: {
		// tabs通知swiper切换
		tabsChange(index) {
			this.swiperCurrent = index;
		},
		// swiper-item左右移动，通知tabs的滑块跟随移动
		transition(e) {
			let dx = e.detail.dx;
			this.$refs.uTabs.setDx(dx);
		},
		// 由于swiper的内部机制问题，快速切换swiper不会触发dx的连续变化，需要在结束时重置状态
		// swiper滑动结束，分别设置tabs和swiper的状态
		animationfinish(e) {
			let current = e.detail.current;
			this.$refs.uTabs.setFinishCurrent(current);
			this.swiperCurrent = current;
			this.current = current;
		},
		// scroll-view到底部加载更多
		onreachBottom() {},
		// 更改关注状态
		changeFollow(index,id){
			this.messageData[index].message.forEach(val=>{
				if(val.id==id){
					val.isFollow = !val.isFollow;
				}
			})
		}
	}
};
</script>

<style lang="scss" scoped>
.top {
	position: relative;
	width: 100%;
	height: 500rpx;
	.bg-img {
		height: 400rpx;
		overflow: hidden;
		position: absolute;
		width: 100%;
	}
	.upload-icon {
		position: absolute;
		top: 20rpx;
		right: 30rpx;
	}
	.avatar-img {
		position: absolute;
		width: 130rpx;
		height: 130rpx;
		border-radius: 5px;
		bottom: 40rpx;
		right: 30rpx;
		z-index: 99;
	}
	.nickname {
		position: absolute;
		bottom: 50rpx;
		right: 180rpx;
		font-size: 14px;
		font-weight: bold;
	}
}
.content {
	.content-top {
		width: 250rpx;
		margin-left: 20rpx;
	}
}
	.message {
		min-height: 58vh;
		.message-content {
			width: 100%;
			height: 100%;
		}
	}
</style>
